:root {
  --primary: #6d327c;
  --secondary: #008094;
  --light: #fff;
  --dark: #000000;
}

body {
  display: flex;

  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* from: https://css-tricks.com/the-trick-to-viewport-units-on-mobile/ */
  height: 100vh; /* Fallback for browsers that do not support Custom Properties */
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  flex: 1 1;
  display: flex;
}

* {
  color: #fff;
  color: var(--light);
}

.o-modal {
  position: absolute;
  background-color: var(--dark);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.m-card {
  background-color: var(--light);
  min-width: 20em;
  max-width: 95%;
  width: 50%;
  border-radius: 2px;
  padding: 2em;
}

.m-card * {
  color: var(--dark);
}

.a-list {
  list-style: none;
  padding: 0;
}

.a-list__item {
  margin: 1em;
}
.m-dice {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.a-dice__roll {
  display: flex;
  box-shadow: 5px 5px 10px -2px #52525280;
  color: white;
  width: 4em;
  margin: 0 auto;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 4em;
}

.a-dice__faces {
  color: #6e6e6e; /* TODO: var */
  margin-left: .5em;
}

.a-dice__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 7em;
  background-color: #ededed; /* TODO: var */
  margin-bottom: 1em;
  padding: 0.5em;
  border-radius: 5em;
  font-size: 0.8em;
}

.a-dice__remove {
  color: white;
  background-color: #cfcfcf; /* TODO: var */
  border: 1px solid black;
  border-radius: 100%;
  width: 1.5em;
  height: 1.5em;
  padding: 0;
  border: 0;
  cursor: pointer;
}

.a-button.-dice-4 {
  background-color: #d49aa4;
  border: 1px solid #9d8085;
}

.a-button.-dice-6 {
  background-color: #5d85d7;
  border: 1px solid #2f5dbc;
}

.a-button.-dice-10 {
  background-color: #b2b27e;
  border: 1px solid #858558;
}

.a-button.-dice-20 {
  background-color: #78be78;
  border: 1px solid #458a45;
}

/* common */
.a-button {
  border: 0;
  background-color: transparent;
  padding: 1em 2em;
  cursor: pointer;
}

.a-button.-outlined {
  display: flex;
  justify-content: space-between;
  border: 2px solid;
}

.a-button.-color {
  background: -webkit-linear-gradient(left, #6d327c, #485DA6, #00a1ba, #36C486);
  background: linear-gradient(to right, #6d327c, #485DA6, #00a1ba, #36C486);
  font-size: 1.5em;
  text-transform: uppercase;
}

.-bg_primary {
  color: var(--light);
  background-color: var(--primary);
}

.-bg_secondary {
  color: var(--light);
  background-color: var(--secondary);
}

/* App */
.o-app {
  display: grid;
  flex: 1 1;
  text-align: center;
  background: var(--light);
  grid-template-columns: 50% 50%;
  grid-template-rows: auto;
  grid-template-areas:
    "substract total"
    "dices dices"
    "actions actions"
  ;
  grid-template-rows:
    minmax(-webkit-min-content, -webkit-max-content)
    auto
    minmax(-webkit-min-content, -webkit-max-content)
  ;
  grid-template-rows:
    minmax(min-content, max-content)
    auto
    minmax(min-content, max-content)
  ;
}

.m-actions {
  grid-area: actions;
}

.a-actions__mainAction {
  width: 100%;
}

.m-actions__secondaryActions {
  display: flex;
  justify-content: space-between;
}

.m-stats__total {
  grid-column: 1;
  grid-area: total;
}

.m-stats__substract {
  grid-column: 2;
  grid-area: substract;
}

.m-stats .-inactive {
  opacity: .5;
}

.a-stats__label {
  font-size: .8em;
  padding: 1em;
}

.a-stats__value {
  font-size: 3em;
  font-weight: 600;
  padding: 0 .3em .3em;
}

.m-dices {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  grid-area: dices;
  padding: 2em;
}

.m-dices__dice {
  margin: 1em;
}

